import '../styles.scss'
import { installManifest, plistMap, platformList } from './constants'

export const downloadAppBtn = document.querySelector('#download-app-btn')
export const pageTitles = document.querySelectorAll('.page-title')

const plist =
  plistMap[new URLSearchParams(window.location.search).get('plistId')]

const platformIsAvailable = platformList.includes(
  navigator.userAgentData.platform
)

const startInstall = () => {
  if (plist && platformIsAvailable) {
    window.location.href = installManifest + encodeURIComponent(plist)
  } else if (!platformIsAvailable) {
    alert('Устройство не поддерживается')
  } else {
    alert('Ссылка на приложение не обнружена')
  }
}
downloadAppBtn.addEventListener('click', startInstall)

if (!plist) {
  downloadAppBtn.setAttribute('disabled', true)

  pageTitles.forEach(
    (title) =>
      (title.innerHTML = 'Ссылка на «РСХБ ДБО ФЛ Учёт надоя» не найдена')
  )
} else {
  pageTitles.forEach(
    (title) =>
      (title.innerHTML =
        'Установка приложения «РСХБ ДБО ФЛ Учёт надоя» на Iphone')
  )
}

window.addEventListener('load', () => {
  downloadAppBtn.click()
})
